@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dangrek&display=swap);

.heading {
    background-color: rgb(5, 97, 5);
    height: 140px;
    display: flex;
}

.head-text {
    color: white;
    font-size: 50px;
    padding: 1rem;
    padding-left: 2rem;
    font-family: 'Source Code Pro', monospace;
    font-weight: 700;
    font-style: italic;
}

.bal {
    margin-left: auto;
    margin-right: 2rem;
    margin-top: 1rem;
    color: white;
    font-size: 20px;
}

.chatrooms {
    margin: 2rem; 
}

.rooms-head {
    font-size: 2rem;
}

.rooms {
    display: flex;
    flex-wrap: wrap;
}

.room-prev {
    margin: 1rem;
    height: 400px;
    width: 300px;    
    box-shadow: 2px 2px 5px rgba(0, 128, 0, 0.616);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.room-prev img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
}

.room-prev .room-name {
    font-size: 1.3rem;
    margin: 1rem 0;
}

.room-creator {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.room-prev-btns {
    display: flex;
    justify-content: space-around;
    width: 100%;    
}

button a {
    text-decoration: none;
    color: black;
}

.room {
  border: solid 1px green;
  width: 50%;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  margin: auto;
  border-radius: 5px;
}

.chatroom-head {
  background-color: rgb(9, 46, 1);
  height: 100px;
  width: 100%;
  color: rgb(168, 151, 151);
  display: flex;
}

.chatroom-head img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin: 1.5rem;
}

.head-info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 1.2rem;
}

.head-name {
  font-size: 1.3rem;
  letter-spacing: 5px;
}

.head-m-count {
  font-size: 0.8rem;
}

.chatroom-main {
  min-height: 75%;
  border: none;
  outline: none;
}

input {
  height: 100%;
  outline: none;
  width: 90%;
  margin: 20px;
}

.send {
  padding: 10px;
}

.send button {
  margin-left: 10px;
  color: white;
  background-color: green;
  font-weight: 400;
}

.chatmessage {
  width: 60%;
  background-color: rgba(128, 128, 128, 0.418);
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  color: rgba(0, 0, 0, 0.774);
}

.chatmessage-time {
  margin-top: 20px;
  color: black;
  font-size: 12px;
}

.my-msg {
  margin-left: auto;
  background-color: rgba(0, 128, 0, 0.438);
}

.chatmessage-body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

* {
    font-family: 'Dangrek', cursive;
}


